@font-face {
    font-family: "Messapia";
    src: url("/assets/fonts/opensource/Messapia/Messapia-Regular.otf") format("opentype");
    font-weight: normal;
  }

@font-face {
  font-family: "Messapia";
  src: url("/assets/fonts/opensource/Messapia/Messapia-Bold.otf") format("opentype");
  font-weight: bold;
}

@font-face {
  font-family: "Lexend";
  src: url("/assets/fonts/opensource/Lexend/Lexend[LXND].ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "CascadiaCode";
  src: url("/assets/fonts/opensource/CascadiaCode/CascadiaCode-Regular-VTT.ttf") format("truetype");
  font-weight: normal;
}

.page-enter-active, .page-leave-active {
  transition: opacity 1s, transform 1s;
}
.page-enter, .page-leave-to {
  opacity: 0;
  /* transform: translateY(-30%); */
}

:root {
  --background: hsl(0, 0%, 100%);
  --color: black;
}

html {
  font-size: 10px;
}

body,
html {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body,
text {
  font-size: 1.6rem;
  font-family: Lexend, Arial, Helvetica, sans-serif;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
  Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
}

body > * {
  flex: 1;
}

a {
  color: var(--color);
  text-decoration: none;
}

h1 {
  font-size: 2em;
}

p {
  font-size: 1.6em;
  margin: 0;
}